/* Start Screen Responsive */
/* Small Screens */
@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}

/* Medium Screens */
@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}

/* Big Screens */
@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}

/* End Screen Responsive */
.container {
  padding: 0 80px;
  margin: 0 auto;
}

* {
  padding: 0%;
  margin: 0;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -moz-box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

h1, h2, h3, h4, body {
  font-family: 'Libre Baskerville', serif;
}

p {
  line-height: 1.7;
  font-family: "Poppins", sans-serif;
}

a {
  font-family: "Poppins", sans-serif;
  color: #313232;
}

input,
textarea {
  outline: none;
}

.App {
  position: relative;
  overflow: hidden;
}

@media (max-width: 768px) {
  .App {
    padding-bottom: 5rem;
  }
}

.logo-container img,
.img-container img {
  width: 100%;
}

.link-container a {
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.link-container a:hover {
  color: #51a644;
}

.section {
  padding-top: 6rem;
}

/* Medium Screens */
@media (max-width: 992px) {
  .section {
    padding-top: 0 !important;
  }
}

.price-container {
  margin: 1.5rem 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  gap: .7rem;
}

.price-container .discounted-price {
  color: #828787;
  font-weight: 700;
  color: #51a644;
}

.awssld .awssld__content {
  background-color: transparent !important;
}

.awssld .awssld__timer {
  opacity: 0 !important;
}

.awssld .awssld__controls button {
  display: none !important;
}

.awssld .awssld__bullets button {
  background-color: #224229 !important;
}

@-webkit-keyframes rotating {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes rotating {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes borderMoving {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.6);
            transform: scale(1.6);
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

@keyframes borderMoving {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.6);
            transform: scale(1.6);
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

@-webkit-keyframes VerticalShake {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  25% {
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
  }
  50% {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
  }
  75% {
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes VerticalShake {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  25% {
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
  }
  50% {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
  }
  75% {
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@-webkit-keyframes HorizontalShaking {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  25% {
    -webkit-transform: translateX(5px);
            transform: translateX(5px);
  }
  50% {
    -webkit-transform: translateX(-5px);
            transform: translateX(-5px);
  }
  75% {
    -webkit-transform: translateX(5px);
            transform: translateX(5px);
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes HorizontalShaking {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  25% {
    -webkit-transform: translateX(5px);
            transform: translateX(5px);
  }
  50% {
    -webkit-transform: translateX(-5px);
            transform: translateX(-5px);
  }
  75% {
    -webkit-transform: translateX(5px);
            transform: translateX(5px);
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

.item-box {
  position: relative;
  width: 280px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: baseline;
      -ms-flex-align: baseline;
          align-items: baseline;
  text-align: center;
  border: solid 1px #eee;
  padding-bottom: 1rem;
}

.item-box .img-container {
  position: relative;
}

.item-box .img-container .onHide {
  opacity: 1;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  z-index: 12;
  position: relative;
}

.item-box .img-container .noHide {
  position: absolute;
  left: 0;
}

.item-box:hover .img-container .onHide {
  opacity: 0;
}

.item-box .other-content {
  width: 100%;
}

.item-box .other-content a {
  font-family: "Poppins", sans-serif;
  font-size: 15px;
  line-height: 18px;
  text-transform: capitalize;
  letter-spacing: .06rem;
}

.item-box .other-content .product-rating {
  margin-top: 1rem;
}

.item-box .icon-container-wrapper {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  position: absolute;
  bottom: 30px;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  opacity: 0;
}

.item-box .icon-container-wrapper .icon-container {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 10px 20px;
  color: #fff;
  background-color: #51a644;
  border-radius: 20px;
  font-size: 14px;
  letter-spacing: .06rem;
  font-family: "Poppins", sans-serif;
  cursor: pointer;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.item-box .icon-container-wrapper .icon-container:hover {
  background-color: #224229;
}

.item-box:hover .icon-container-wrapper {
  opacity: 1;
}
