}

.shop-section .main-content .rightSide .row .box-wrapper-list-view .price-cart {
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

.shop-section .main-content .sorting-bar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-bottom: 3em;
}

.shop-section .main-content .sorting-bar svg {
  font-size: 18px;
  cursor: pointer;
}

.shop-section .main-content .sorting-bar svg:hover, .shop-section .main-content .sorting-bar svg:focus {
  color: #51a644;
}

.shop-section .main-content .sorting-bar .paragraph-content {
  color: #999;
