.shop-section .main-content .sorting-bar .sorting-side select {
  border: solid 1px transparent;
  padding: 10px 20px;
  background: #fff8e7;
}

.shop-section .main-content .sorting-bar .sorting-side .icon-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: reverse;
      -ms-flex-direction: row-reverse;
          flex-direction: row-reverse;
